<template>
   <v-container>
      <v-flex>
        <div style="padding-bottom:10px" v-if="estadoproceso != 'Finalizado'">
            <v-btn fab dark small color="primary" @click="abrirDialogoAddedit(0, '')" >
              <v-icon dark>add</v-icon>
            </v-btn>
        </div>
        <v-data-table :headers="headers"
            :items="data"
            :items-per-page="5"
            :disable-sort="true"
        >
            <template v-slot:item.action="{ item }">
                <v-icon
                    class="mr-5"
                    color="primary"
                    @click="abrirDialogoAddedit(1, item)"
                    v-if="estadoproceso != 'Finalizado'"
                >
                    edit
                </v-icon>
                <v-icon
                    class="mr-5"
                    color="error"
                    @click="dialog_del=true, itemactual=item"
                    v-if="estadoproceso != 'Finalizado'"
                >
                    delete
                </v-icon>
            </template>
        </v-data-table>
      </v-flex>
      <v-dialog v-model="dialog" max-width="700px">
        <v-card>
            <v-card-title>
              <span class="headline">{{ formTitle }}</span>
            </v-card-title>
            <v-card-text>
                <v-container grid-list-md>
                    <v-layout wrap column>
                        <v-form v-model="valid" ref="form">
                            <v-flex>
                                <v-text-field v-model="descripcion"
                                    label="Descripción"
                                    :rules="[rules.required]"
                                >
                                </v-text-field>
                            </v-flex>
                        </v-form>
                    </v-layout>
                </v-container>
            </v-card-text>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" text @click="cerrarDialogo">Cancelar</v-btn>
                <v-btn color="primary" text @click="saveDocumento" :disabled="!valid">Guardar</v-btn>
            </v-card-actions>
        </v-card>
      </v-dialog>

      <v-dialog v-model="dialog_del" max-width="600px">
        <v-card>
            <v-card-title>
                <span class="headline red--text">Eliminar Documento</span>
            </v-card-title>
            <v-card-text>
                ¿Esta seguro de eliminar el actual documento?
            </v-card-text>
            <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" dark text @click="dialog_del=false">Cancelar</v-btn>
                <v-btn color="red" text @click="deleteDoc" >Eliminar</v-btn>
            </v-card-actions>
        </v-card>
      </v-dialog>
    </v-container>
</template>
<script>
import { adminApp } from '@/services/api'
export default {
  name: 'tabdoclegalizacontractual',
  props: ['idproceso', 'estadoproceso'],
  data () {
    return {
      data: [],
      dialog: false,
      dialog_del: false,
      itemactual: '',
      formTitle: '',
      edit: -1,
      valid: false,
      codigo: 0,
      tipo: 'LC',
      descripcion: '',
      headers: [
        { text: 'Documento', align: 'left', value: 'descripcion', class: 'subtitle-1 font-weight-bold black--text' },
        { text: 'Acción', value: 'action', sortable: false, class: 'subtitle-1 font-weight-bold black--text' }
      ],
      rules: {
        required: value => !!value || 'Campo requerido.'
      }
    }
  },
  methods: {
    limpiarDatos () {
      this.codigo = 0
      this.descripcion = ''
    },
    async traeDocumentos () {
      try {
        const { data } = await adminApp.PropuestaConvocatoriaDocumentos({ propuestaconvocatoria: this.idproceso, tipo: this.tipo })
        this.data = data
      } catch (error) {
        console.log(error)
      } finally {
      }
    },
    abrirDialogoAddedit (edit, item) {
      this.edit = edit
      this.itemactual = item
      this.dialog = true
    },
    abrirDialogoDel (item) {
      this.itemactual = item
      this.dialog_del = true
    },
    async deleteDoc () {
      try {
        const { data } = await adminApp.eliminarPropuestaConvocatoriaDocumento({ codigo: this.itemactual.codigo })
        if (data) {
          this.traeDocumentos()
          this.$toasted.success('Se eliminó el documento con éxito', { icon: 'done', duration: 5000 })
          this.itemactual = ''
          this.dialog_del = false
        } else {
          this.$toasted.error('Se presentó un error al intentar eliminar el documento', { icon: 'error', duration: 5000 })
        }
      } catch (error) {
        console.log(error)
      } finally {
      }
    },
    async saveDocumento () {
      let ok = 0
      try {
        if (this.edit === 0) {
          const { data } = await adminApp.agregarPropuestaConvocatoriaDocumento(
            {
              propuestaconvocatoria: this.idproceso,
              descripcion: this.descripcion,
              tipo: this.tipo
            })
          console.log(data)
          if (data) {
            ok = 1
            this.$toasted.success('Se agregó el documento con éxito', { icon: 'done', duration: 5000 })
            this.itemactual = ''
            this.dialog_del = false
          } else {
            this.$toasted.error('Se presentó un error al realizar el proceso', { icon: 'error', duration: 5000 })
          }
        } else {
          if (this.itemactual.descripcion === this.descripcion) {
            this.$toasted.error('No hay datos para actualizar', { icon: 'error', duration: 5000 })
            return
          }
          const { data } = await adminApp.actualizarPropuestaConvocatoriaDocumento(
            {
              codigo: this.codigo,
              descripcion: this.descripcion,
              propuestaconvocatoria: this.idproceso,
              tipo: this.tipo
            })
          if (data) {
            ok = 1
            this.$toasted.success('Se modificó el documento con éxito', { icon: 'done', duration: 5000 })
            this.itemactual = ''
            this.dialog_del = false
            this.traeDocumentos()
          } else {
            this.$toasted.error('Se presentó un error al realizar el proceso', { icon: 'error', duration: 5000 })
          }
        }
        if (ok) {
          this.itemactual = ''
          this.edit = -1
          this.dialog = false
        }
      } catch (error) {
        let mensajeErr = ''
        if (error.response) {
          if (error.response.data) {
            mensajeErr = error.response.data.message
          }
        }
        if (mensajeErr === '') {
          mensajeErr = 'Se presentó un error al realizar el proceso'
        }
        this.traeDocumentos()
        this.$toasted.error(mensajeErr, { icon: 'error', duration: 5000 })
      }
    },
    cerrarDialogo () {
      this.itemactual = ''
      this.edit = -1
      this.dialog = false
    }
  },
  watch: {
    idproceso: function (val) {
      this.traeDocumentos()
    },
    edit: function (val) {
      if (val) {
        this.formTitle = 'Editar'
        this.codigo = this.itemactual.codigo
        this.descripcion = this.itemactual.descripcion
      } else {
        this.limpiarDatos()
        this.formTitle = 'Nuevo'
      }
    }
  },
  mounted () {
    this.traeDocumentos()
  }
}
</script>
